.container{max-width: 1140px;}
.container1{max-width: 1350px;margin: auto;background:#160092}
.container2{margin: auto;background:#160092}

body{background-image: linear-gradient(to right, rgb(29, 4, 152), rgb(52, 31, 151));}
.navbar{background-color: #160092;}
.navbar ul li a {color: azure;}
.navbar ul button{width: 80px;height: 35px;border-radius: 13px;background-image:linear-gradient(to right, rgb(29, 4, 152), rgb(52, 31, 151)) ;border-color: blue;margin: 5px 5px;}
.content1{background-image: url('back.png');width: auto;height: 100vh;
  background-position-x:right;background-position-y:bottom;z-index: 2;background-attachment: fixed;}

.C1{position: relative;left: 310px;top: 130px;}
.C1 h1{font-weight:600;}
.C1small{border: 1px solid transparent;padding: 6px 10px;background-image: linear-gradient(to bottom right,#766ee6 0,#3540b0);border-radius: 15px;}
.C1small:hover a {color: white;}
.C1button button{background-image: linear-gradient(90deg,#21d397 0,#7450fe 51%,#21d397);border-radius: 13px;padding:2px 16px;border: 1px solid transparent;}
.aboutbutton button{background-image: linear-gradient(90deg,#21d397 0,#7450fe 51%,#21d397) ;border-radius: 14px;border: 1px solid transparent;}
.servicebox .box{border: 1px solid #08b1b1;width: 350px;height: 288px;margin: 0 15px;border-radius: 0px 25px;}
.servicebox .box1{margin: 30px 0}
.servicebox .box1 h5 {color: white;}
.servicebox .box1 p{color: var(--variable1);}
.application .box11 p{color:var(--variable1) ;}
.application .box2 .box{margin-left: 230px;}
.application .box2 .box22{margin-left: 300px;}
.application .box21 p {color:var(--variable1) ;}
.box2{position: relative;}
.box22img{position: absolute;top: 40px;right: 280px;z-index: -3;}
.contact{background-image:url('back1.png') ; height: 650px;background-position-x: right;}
.contact form input{width: 300px;height: 30px;background-color: aliceblue;}

.contact p{color: var(--variable1);}
.footer{background-image: url('footer.png');background-repeat: no-repeat;height: 348px;background-size: cover;}
footer ul{list-style: none;}
footer p{color: var(--variable1);}
footer ul li {color:  var(--variable1);}
.about p{color:var(--variable1) ;}
:root{
  --variable1:hsla(0,0%,100%,.7) ;
}

@media (max-width:700px)
{
  .navbar{height: 290px;}
  .navbar .imghead{margin-bottom: 230px;width: 45px;}
  .navbar ul li a{font-size: 12px;}
  .navbar ul{margin-right: 200px}
  .navbar button{position: relative;left: -20px; bottom: 3px;}
  #navbarText{width: auto;}
  .Content .C1{position:static}
  .content1{margin-top: 40px;}
}
